import uniIcons from '@/components/uni-icons/uni-icons.vue'

export default {
	components: {
		uniIcons
	},
	name: "search",
	props: {
		sortName: {
			type: String,
			requied: true
		},
		searchKeyWord: {
			type: String,
			requied: true
		},
		sortShow: {
			type: Boolean,
			requied: true
		}
	},
	methods: {
		gotoSearch() {
			uni.navigateTo({
				url: `/pages/search/search`
			});
		},
		showSortOption() {
			this.$emit('showSortOption')
		},
		clearSearchKeyWord() {
			this.$emit('clearSearchKeyWord')
		}
	},
	computed: {
		canClear() {
			return this.searchKeyWord !== '搜索'
		}
	},
	data() {
		return {

		};
	}
}
